	module.exports = {"data":{"markdownRemark":{"html":"<h1>HTML Attributes</h1>\n<p>HTML elements can have attributes, which contain additional information about the element.</p>\n<p>HTML attributes generally come in name-value pairs, and always go in the opening tag of an element. The attribute name says what type of information you’re providing about the element, and the attribute value is the actual information.</p>\n<p>For example, an anchor (<code>&#x3C;a></code>) element in an HTML document creates links to other pages, or other parts of the page. You use the <code>href</code> attribute in the opening <code>&#x3C;a></code> tag to tell the browser where the link sends a user.</p>\n<p>Here’s an example of a link that sends users to freeCodeCamp’s home page:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span> <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>www.freecodecamp.org<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Click here to go to freeCodeCamp!<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Notice that the attribute name (<code>href</code>) and value (“www.freeCodeCamp.org”) are separated with an equals sign, and quotes surround the value.</p>\n<p>There are many different HTML attributes, but most of them only work on certain HTML elements. For example, the <code>href</code> attribute won’t work if it’s placed in an opening <code>&#x3C;h1></code> tag.</p>\n<p>In the example above, the value supplied to the <code>href</code> attribute could be any valid link. However, some attributes only have a set of valid options you can use, or values need to be in a specific format. The <code>lang</code> attribute tells the browser the default language of the contents in an HTML element. The values for the <code>lang</code> attribute should use standard language or country codes, such as <code>en</code> for English, or <code>it</code> for Italian.</p>\n<h2>Boolean Attributes</h2>\n<p>Some HTML attributes don’t need a value because they only have one option. These are called Boolean attributes. The presence of the attribute in a tag will apply it to that HTML element. However, it’s okay to write out the attribute name and set it equal to the one option of the value. In this case, the value is usually the same as the attribute name.</p>\n<p>For example, the <code>&#x3C;input></code> element in a form can have a <code>required</code> attribute. This requires users to fill out that item before they can submit the form.</p>\n<p>Here are examples that do the same thing:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>text<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">required</span> <span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>text<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">required</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>required<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h2>Other Resources</h2>\n<p><a href=\"#\">HTML links</a>\n<a href=\"#\">Href Attribute</a>\n<a href=\"#\">Lang Attribute</a>\n<a href=\"#\">HTML Input Element</a>\n<a href=\"#\">Required Attribute</a></p>","fields":{"slug":"/html/attributes/"},"frontmatter":{"title":"Attributes"}}},"pathContext":{"slug":"/html/attributes/"}}

